import React, { useState, useEffect } from 'react';
import { IconButton, Tooltip } from '@mui/material';
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import StopIcon from '@mui/icons-material/Stop';

interface TextToSpeechButtonProps {
  text: string;
}

const TextToSpeechButton: React.FC<TextToSpeechButtonProps> = ({ text }) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [speechInstance, setSpeechInstance] = useState<SpeechSynthesisUtterance | null>(null);
  const [voices, setVoices] = useState<SpeechSynthesisVoice[]>([]);

  //     
  useEffect(() => {
    const loadVoices = () => {
      if (window.speechSynthesis) {
        const availableVoices = window.speechSynthesis.getVoices();
        setVoices(availableVoices);
      }
    };

    //   
    loadVoices();

    // Chrome       
    if (window.speechSynthesis) {
      window.speechSynthesis.onvoiceschanged = loadVoices;
    }

    //    
    return () => {
      if (window.speechSynthesis) {
        window.speechSynthesis.onvoiceschanged = null;
        window.speechSynthesis.cancel();
      }
    };
  }, []);

  //     
  const handleSpeak = () => {
    if (isPlaying) {
      stopSpeech();
    } else {
      speak();
    }
  };

  const speak = () => {
    if (!window.speechSynthesis) {
      alert('     .');
      return;
    }

    //      
    window.speechSynthesis.cancel();

    //    
    const utterance = new SpeechSynthesisUtterance(text);
    
    //   
    utterance.lang = 'ko-KR';
    utterance.rate = 0.9; //   ( 1)
    
    //      
    const koreanVoice = voices.find(voice => voice.lang.includes('ko'));
    if (koreanVoice) {
      utterance.voice = koreanVoice;
    }
    
    //   
    utterance.onend = () => {
      setIsPlaying(false);
      setSpeechInstance(null);
    };
    
    utterance.onerror = () => {
      setIsPlaying(false);
      setSpeechInstance(null);
    };
    
    //   
    window.speechSynthesis.speak(utterance);
    setIsPlaying(true);
    setSpeechInstance(utterance);
  };

  const stopSpeech = () => {
    if (window.speechSynthesis) {
      window.speechSynthesis.cancel();
      setIsPlaying(false);
      setSpeechInstance(null);
    }
  };

  return (
    <Tooltip title={isPlaying ? " " : " "}>
      <IconButton onClick={handleSpeak} size="small">
        {isPlaying ? <StopIcon /> : <VolumeUpIcon />}
      </IconButton>
    </Tooltip>
  );
};

export default TextToSpeechButton;
